import React, { useEffect, useState } from "react";
import Carouselcomp from '../components/carousel/index.tsx';
import ProductSlider from '../components/product_slider/ProductSlider.tsx';
// import {fetchImage, fetchProduct} from '../actions/apiUtil.ts';
import axios from "axios";
import './slider.css'
const BASE_URL = 'https://little-feet.s3.us-west-1.amazonaws.com/'
const LandingPage: React.FC = () => {
  const [images, setImages] = useState([]);
  const [products, setProducts] = useState([]);
  const [whowe, setWhoWe] =useState([]);
  useEffect(() => {
    const fetchData = async()=>{
      try{
        const response = await axios.get(`${process.env.REACT_APP_API_URL}/api/image/?start=1&end=10`, {withCredentials:true});
        console.log(response)
        if(response.status === 200){
          const data = await response.data;
          console.log(data)
          const filePaths = data.map(image => BASE_URL + image);
          console.log(filePaths)
          setImages(filePaths);
        }else{
          console.log('didnt set path?', response)
        }
        
      }catch(e){
        console.error('Error while fetching images', e);
      }
      
      // console.log(imagePaths)
      // setImages(imagePaths);
      const productData = await axios.get(`${process.env.REACT_APP_API_URL}/api/featured/`, {withCredentials:true});
      setProducts(productData.data);
      const whoWe = await axios.get(`${process.env.REACT_APP_API_URL}/api/who/`, {withCredentials:true});
      setWhoWe(whoWe.data);
    }
    fetchData();
  }, []);
  let we = {__html:`${whowe}`}
  return (
    <div className="App">
      {images.length > 0 ? (
        <>
          <Carouselcomp images={images} />
          <h3>Featured Products</h3>
          <div className='slider'>
            <ProductSlider products={products} />
          </div>
          <h3>
            Who Are We?
          </h3>
          <div dangerouslySetInnerHTML={{ __html: `${whowe}` }} />
        </>
      ) : (
        <p>Loading images...</p>
      )}
    </div>
  );
}

export default LandingPage;