import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './slider.css';

const HowTo = () => {
  const [howTo, setHowTo] = useState();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchHowTo = async () => {
      try {
        const response = await axios.get(`${process.env.REACT_APP_API_URL}/api/how-to/`, { withCredentials: true });
        setHowTo(response.data);
      } catch (error) {
        console.error('Error fetching How To data:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchHowTo();
  }, []);

  let description = { __html: `${howTo}` };

  return (
    <div className='howTo'>
      {loading ? (
        <p>Loading...</p>
      ) : (
        <div className='who-are-we' dangerouslySetInnerHTML={description} />
      )}
    </div>
  );
};

export default HowTo;
